<template>
  <div class="roles">
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
  </div>
</template>

<script>
import Tables from 'views/common/tables'
import queryString from "querystring"

const dataTable = {
  table: [
    { label:'角色ID', prop:'id'},
    { label:'角色名称', prop:'sign'},
  ],
  edit: [
    {name: '权限', id: 't1'},
  ]
}

export default {
  name: 'systemRole',
  data() {
    return {
      dataTable: dataTable,
      tableList: [],
      loading: false,
    }
  },
  components: {
    Tables
  },
  mounted(){
    this.search();
  },
  methods:{
    tabchange(params){
      console.log(params.id + '--id被单击了')

      this.id = params.data.id;
      // 编辑
      if(params.id === 't1') return this.$router.push(`/systemManage/roleAdd?type=edit&id=${this.id}`);
    },
    search(){  //搜索
      this.loading = true;
      this.api.get(`/role`).then((res)=>{
        this.loading = false;
        this.dataInit(res.data);
        this.tableList = res.data;
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    dataInit(data){//数据初始化
    }
  }
}
</script>
 
<style lang="less" scoped>
.roles{
    margin: 20px;
}
</style>
 